.cayenne-wrapper {
  border: solid 1px black;
}

@media (max-width: 900px) {
  .cayenne-wrapper .nav .burger-arrow {
    display: block;
  }
}

.cayenne-wrapper .header {
  position: relative;
  background-color: inherit;
  max-width: 1440px;
}

.cayenne-page {
  background-color: #e5fcf5;
}

.cayenne-section {
  padding-block-start: 135px;
  padding-block-end: 80px;
  padding-inline: 80px;
  border-bottom: solid 1px black;
}
@media (max-width: 900px) {
  .cayenne-section {
    padding-block-start: 80px;
    padding-inline: 0px;
    padding-block-end: 0px;
  }
}

.cayenne-body {
  position: relative;
  display: flex;
  align-items: center;
  gap: 60px;
}
@media (max-width: 900px) {
  .cayenne-body {
    flex-direction: column;
    width: 100%;
  }
}

.cayenne-arrow {
  position: absolute;
  top: -110px;
}
@media (max-width: 900px) {
  .cayenne-arrow {
    display: none;
  }
}

.cayenne_body-title {
  max-width: 625px;
}
@media (max-width: 900px) {
  .cayenne_body-title {
    padding-inline: 25px;
  }
}

.cayenne_body-title p {
  font-size: 30px;
  font-weight: 400;
  font-family: Nunito Sans, sans-serif;
  padding-block-start: 5px;
}
@media (max-width: 900px) {
  .cayenne_body-title p {
    font-size: 22px;
  }
}

.cayenne-logo {
  width: 100%;
  max-width: 330px;
  height: auto;
}

.cayenne_body-burger {
  max-width: 594px;
  max-height: 812px;
  border-radius: 10px;
  overflow: hidden;
  object-fit: contain;
  border: solid 1px black;
}
@media (max-width: 900px) {
  .cayenne_body-burger {
    max-height: 215px;
  }
}

.cayenne-burger {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}
.cayenne_section-crafted {
  padding-block: 80px;
  padding-inline-start: 190px;
  padding-inline-end: 100px;
  border-bottom: solid 1px black;
}
@media (max-width: 900px) {
  .cayenne_section-crafted {
    padding-inline: 0px;
    padding-block: 60px;
  }
}

.crafted_body-column {
  display: flex;
  gap: 80px;
  align-items: center;
}
@media (max-width: 900px) {
  .crafted_body-column {
    flex-direction: column-reverse;
    gap: 20px;
  }
}

.crafted-body {
  display: flex;
  align-items: center;
  justify-content: center;
}

.crafted_body-img {
  border-radius: 10px;
  border: solid 1px black;
  max-width: 500px;
  height: auto;
  overflow: hidden;
}

.crafted-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.crafted_body-title {
  flex: 1;
  max-width: 600px;
}
@media (max-width: 900px) {
  .crafted_body-title {
    padding-inline: 25px;
  }
}

.crafted_body-title h2 {
  font-size: 65px;
}
@media (max-width: 900px) {
  .crafted_body-title h2 {
    font-size: 35px;
    padding-block-end: 25px;
  }
}

.crafted_body-title p {
  font-size: 26px;
  font-weight: 400;
  font-family: Nunito Sans, sans-serif;
}
@media (max-width: 900px) {
  .crafted_body-title p {
    font-size: 20px;
  }
}

.menu_body-title {
  padding-block: 35px;
  padding-inline: 80px;
  background-color: white;
  border-bottom: solid 1px black;
}
@media (max-width: 900px) {
  .menu_body-title {
    padding-inline: 25px;
  }
}

.menu_body-title h1 {
  font-size: 65px;
}
@media (max-width: 900px) {
  .menu_body-title h1 {
    font-size: 32px;
  }
}

.cayenne_section-cards {
  padding-block-start: 60px;
  border-bottom: solid 1px black;
  padding-inline: 80px;
}
@media (max-width: 900px) {
  .cayenne_section-cards {
    padding-inline: 25px;
    padding-block-start: 0;
  }
}

.mealco_cards-title {
  padding-block-end: 40px;
}
@media (max-width: 900px) {
  .mealco_cards-title {
    padding-block-end: 20px;
  }
}

@media (max-width: 900px) {
  .mealco_cards-title h2 {
    font-size: 20px;
  }
}

.sandwiches-cards,
.sides-cards,
.beverage-cards {
  padding-block-end: 100px;
}
@media (max-width: 900px) {
  .sandwiches-cards,
  .sides-cards,
  .beverage-cards {
    padding-block-end: 0px;
  }
}

.body_card-title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 15px;
}

.body_card-title h2 {
  font-size: 24px;
}

.body_card-title p {
  font-size: 22px;
  font-weight: 400;
  font-family: Nunito Sans, sans-serif;
}

.cayenne_section-menu {
  padding-block-end: 40px;
  display: none;
}
@media (max-width: 600px) {
  .cayenne_section-menu {
    display: block;
  }
}

.cayenne_menu-items {
  display: flex;
  gap: 50px;
  overflow: auto;
  height: 50px;
}

.cayenne_menu-item {
  position: relative;
}

.cayenne_menu-item button {
  font-size: 20px;
  font-weight: 700;
  opacity: 0.5;
  background: none;
  border: none;
  cursor: pointer;
}

.cayenne_menu-item.active button {
  opacity: 1;
}

.cayenne_menu-item.active::after {
  position: absolute;
  content: "";
  bottom: 5px;
  left: 45%;
  width: 8px;
  height: 8px;
  background-color: black;
  border-radius: 100%;
}

.mealco_card-text h3 {
  font-size: 16px;
}